@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar{
     display: flex;
     justify-content: space-between;
     align-items: center;
     padding: 1.25rem .3rem;
}
 .aClose{
     display: inline-block;
     margin: 0 0.5rem;
     -webkit-animation: rotateOut;
     animation: rotateOut;
     -webkit-animation-duration: 0.4s;
     animation-duration: 0.4s;
}
 .aBars{
     display: inline-block;
     margin: 0 0.5rem;
     -webkit-animation: flip;
     animation: flip;
     -webkit-animation-duration: 0.4s;
     animation-duration: 0.4s;
}
 nav ul{
     display: flex;
}
 nav li{
     font-family: 'Rubik', sans-serif;
     font-weight: 500;
     font-size: .7rem;
     margin:0 1.2rem;
}
 nav li:not(:last-child) a{
     color: rgba(0,0,0,0.7);
     transition: 0.4s;
}
 nav li a:hover{
     color: rgba(0,0,0,1);
     color: hsl(0, 94%, 66%);
}
 nav .lbtn{
     background-color: hsl(0, 94%, 66%);
     box-shadow: 0 2px 5px 1px rgba(0,0,0,0.1);
     font-family: 'Rubik', sans-serif;
     color: rgba(255,255,255,0.7);
     padding: .5rem 1.5rem;
     border-radius: 5px;
     font-size: .7rem;
     font-weight: 500;
     transition: 0s;
     border: 1px solid transparent;
     transition: 0.4s ease;
}
 nav .lbtn:hover{
     background-color: transparent;
     color: hsl(0, 94%, 66%);
     border:1px solid hsl(0, 94%, 66%);
}
 .navbar .menu,.navbar .Msocial{
     display: none;
     color: #000;
}
 .navbar i{
     cursor: pointer;
}
 .fa-close{
     color: #fff;
}
 .dn{
     display: none;
}
 @media all and (max-width: 774px){
     nav{
         display: none;
         position: fixed;
         justify-content: center;
         align-items: center;
         top: 0;
         width: 100%;
         height: 100%;
        /*min-height: 700px;
        */
         left: 0;
         background-color: rgba(64,70,96,0.9);
         z-index: +900;
         overflow-y: scroll;
    }
     .open{
         display: flex;
         -webkit-animation: slideInLeft;
         animation: slideInLeft;
         -webkit-animation-duration: 0.4s;
         animation-duration: 0.4s;
    }
     .open ul{
         display: block;
    }
     .open ul li a{
         color: #fff;
    }
     nav ul{
         display: block;
         width: 100%;
         margin: auto;
    }
     nav ul li{
         width: 80%;
         margin: 0 auto;
         text-align: center;
         font-size: 1.2rem;
         padding:1em .2em;
         border-top:1px solid rgba(255,255,255,0.2);
    }
     nav ul li a{
         width: 100%;
         color: #fff;
    }
     nav ul li:not(:last-child):hover{
         background-color: rgba(255,255,255,0.7);
    }
     nav .lbtn{
         font-size: 1.5rem;
         background-color: transparent;
         border:1px solid #fff;
         width: 100%;
         display: block;
    }
     .navbar .menu{
         display: block;
         z-index: +999;
    }
     .navbar .bnav{
         z-index: +999;
    }
     .navbar .w{
         -webkit-filter: brightness(0.4) invert(10);
         filter: brightness(0.4) invert(10);
    }
     .navbar .Msocial{
         display: flex;
         justify-content: space-between;
         position: fixed;
         left: 50%;
         transform: translateX(-50%);
         top: 90%;
         z-index: +999;
    }
     .navbar .Msocial a{
         margin: 0 1em;
         color: #fff;
    }
}

 .header{
     display: flex;
     margin-top:8em;
     justify-content: space-between;
}
 .header .description{
