}
 .header .hero img{
     width: 100%;
     height: auto;
     transform: translate(5rem,-3rem);
    /*margin: -3rem 5rem;
    */
}
 .bluebox{
     display: flex;
     position: absolute;
     left: 66vw;
     top:320px;
     width: 50vw;
     height: 300px;
     background-color: #5355EE;
     border-bottom-left-radius: 150px;
