 *{
    /*border:1px solid black;
    */
}
 @media all and (max-width: 774px){
     .header{
         margin:2em 0;
         flex-direction: column-reverse;
         text-align: center;
    }
     .header .description h1{
         font-size: 1.3rem;
    }
     .header .hero img{
         transform: translate(0);
    }
